@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.welcome {
  width: 100%;
  height: 100vh;
  background-color: black;
  color: white;
  font-family: "Open Sans";
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  position: relative; }

.welcomeMsg {
  font-size: 3em; }

@media (max-width: 1000px) {
  .welcomeMsg {
    font-size: 2em; } }

@media (max-width: 600px) {
  .welcomeMsg {
    font-size: 1em; } }

.welcomeMsg p {
  overflow: hidden;
  border-right: .15em solid transparent;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: .15em;
  -webkit-animation: typing 16s steps(40, end) infinite, blink-caret 0.75s step-end infinite;
  animation: typing 16s steps(40, end) infinite, blink-caret 0.75s step-end infinite; }

.welcomeMsg p:after {
  content: '';
  display: block;
  -webkit-animation: change-letter 16s linear infinite;
  animation: change-letter 16s linear infinite; }

@-webkit-keyframes change-letter {
  0% {
    content: "WELCOME TO MY SITE"; }
  50% {
    content: "WEB DEVELOPMENT PRESTIGE"; }
  100% {
    content: "WEB DEVELOPMENT PRESTIGE"; } }

@keyframes change-letter {
  0% {
    content: "WELCOME TO MY SITE"; }
  50% {
    content: "WEB DEVELOPMENT PRESTIGE"; }
  100% {
    content: "WEB DEVELOPMENT PRESTIGE"; } }

@-webkit-keyframes typing {
  0% {
    width: 0; }
  7% {
    width: 100%; }
  25% {
    width: 100%; }
  25.00001% {
    width: 0; }
  33% {
    width: 100%; }
  100% {
    width: 100%; } }

@keyframes typing {
  0% {
    width: 0; }
  7% {
    width: 100%; }
  25% {
    width: 100%; }
  25.00001% {
    width: 0; }
  33% {
    width: 100%; }
  100% {
    width: 100%; } }

@-webkit-keyframes blink-caret {
  from, to {
    border-color: transparent; }
  50% {
    border-color: white; } }

@keyframes blink-caret {
  from, to {
    border-color: transparent; }
  50% {
    border-color: white; } }

.arrow {
  position: absolute;
  left: 50%;
  bottom: 10%;
  font-size: 80px;
  font-weight: bold;
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
  cursor: pointer;
  color: gray;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  -webkit-animation: bounce-arrow .5s alternate infinite;
  animation: bounce-arrow .5s alternate infinite; }

.arrow:hover {
  color: white; }

@-webkit-keyframes bounce-arrow {
  0% {
    bottom: 10%; }
  100% {
    bottom: 8%; } }

@keyframes bounce-arrow {
  0% {
    bottom: 10%; }
  100% {
    bottom: 8%; } }



