@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.menu {
  position: fixed;
  top: 0;
  display: none;
  width: 100%;
  z-index: 2; }

nav {
  width: 100%;
  -webkit-mask-image: -webkit-gradient(linear, left top, right top, from(rgba(116, 116, 116, 0.5)), color-stop(25%, #ffffff), color-stop(75%, #ffffff), to(rgba(116, 116, 116, 0.5)));
  -webkit-mask-image: -webkit-linear-gradient(left, rgba(116, 116, 116, 0.5) 0%, #ffffff 25%, #ffffff 75%, rgba(116, 116, 116, 0.5) 100%);
  mask-image: -webkit-gradient(linear, left top, right top, from(rgba(116, 116, 116, 0.5)), color-stop(25%, #ffffff), color-stop(75%, #ffffff), to(rgba(116, 116, 116, 0.5)));
  mask-image: linear-gradient(90deg, rgba(116, 116, 116, 0.5) 0%, #ffffff 25%, #ffffff 75%, rgba(116, 116, 116, 0.5) 100%);
  margin: 0;
  padding: 0; }

nav ul {
  text-align: center;
  background: -webkit-gradient(linear, left top, right top, from(white), color-stop(35%, black), color-stop(65%, black), to(white));
  background: -webkit-linear-gradient(left, white 0%, black 35%, black 65%, white 100%);
  background: -o-linear-gradient(left, white 0%, black 35%, black 65%, white 100%);
  background: linear-gradient(90deg, white 0%, black 35%, black 65%, white 100%);
  -webkit-box-shadow: 0 0 25px rgba(0, 0, 0, 0.5), inset 0 0 1px rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.5), inset 0 0 1px rgba(255, 255, 255, 0.5); }

nav ul li {
  display: inline-block; }

nav ul li a {
  padding: 18px;
  font-family: "Open Sans";
  text-transform: uppercase;
  color: white;
  font-size: 18px;
  text-decoration: none;
  display: block; }

nav ul li a::after {
  content: "";
  display: block;
  border-bottom: 3px solid white;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  width: 0; }

nav ul li:hover a::after {
  width: 100%; }



