@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.aboutMeContainer {
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  background-color: white;
  -ms-flex-pack: center;
  justify-content: center; }

.portfolioImage {
  -o-object-fit: contain;
  object-fit: contain;
  -ms-flex-item-align: end;
  align-self: flex-end; }

.contentBox {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  height: 80%;
  width: 50%;
  -ms-flex-item-align: end;
  align-self: flex-end; }

.content {
  width: 60%;
  height: 50%;
  position: relative;
  -ms-flex-item-align: end;
  align-self: flex-end;
  margin-bottom: 300px;
  font-family: "Open Sans";
  font-size: 30px;
  text-align: center; }

@media (max-width: 1000px) {
  .contentBox {
    width: 70%; }
  .content {
    font-size: 25px; } }

@media (max-width: 600px) {
  .contentBox {
    width: 100%; } }

@media (max-width: 400px) {
  .content {
    font-size: 20px; } }



